import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'empty-state',
    shadow: true,
    styleUrl: 'empty-state.scss',
})
export class EmptyState {
    @Prop() public text: string;
    @Prop() public icon: string = 'nothing_found';

    public render() {
        return (
            <div class="empty-state">
                <limel-icon name={this.icon} />
                <p>{this.text}</p>
            </div>
        );
    }
}
